import { Header } from "@/components/header"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Search, AlertTriangle, TrendingDown, TrendingUp, Package } from "lucide-react"

export default function InventarioPage() {
  const insumos = [
    {
      id: 1,
      nombre: "Café en Grano",
      stock: 150.5,
      minimo: 50,
      unidad: "kg",
      tendencia: "up",
      proveedor: "Café Premium",
    },
    {
      id: 2,
      nombre: "Leche Entera",
      stock: 35.0,
      minimo: 50,
      unidad: "L",
      tendencia: "down",
      proveedor: "Lácteos del Valle",
    },
    { id: 3, nombre: "Azúcar", stock: 200.0, minimo: 100, unidad: "kg", tendencia: "up", proveedor: "Dulces SA" },
    {
      id: 4,
      nombre: "Harina",
      stock: 15.5,
      minimo: 50,
      unidad: "kg",
      tendencia: "down",
      proveedor: "Molino del Norte",
    },
    {
      id: 5,
      nombre: "Chocolate",
      stock: 120.0,
      minimo: 80,
      unidad: "kg",
      tendencia: "up",
      proveedor: "Chocolate Fino",
    },
    {
      id: 6,
      nombre: "Vasos Desechables",
      stock: 500,
      minimo: 1000,
      unidad: "unidad",
      tendencia: "down",
      proveedor: "Empaques Eco",
    },
  ]

  const stockBajo = insumos.filter((item) => item.stock < item.minimo).length

  return (
    <div className="min-h-screen bg-background">
      <Header />

      <main className="container mx-auto px-4 py-8">
        <h2 className="text-3xl font-bold mb-2">Gestión de Inventario</h2>
        <p className="text-muted-foreground mb-8 text-pretty">Control de insumos y stock de tu cafetería</p>

        {stockBajo > 0 && (
          <Card className="mb-6 border-destructive/50 bg-destructive/5">
            <CardContent className="pt-6">
              <div className="flex items-center gap-3">
                <div className="flex h-10 w-10 items-center justify-center rounded-full bg-destructive/10">
                  <AlertTriangle className="h-5 w-5 text-destructive" />
                </div>
                <div>
                  <p className="font-semibold text-foreground">
                    {stockBajo} insumo{stockBajo > 1 ? "s" : ""} con stock bajo
                  </p>
                  <p className="text-sm text-muted-foreground">Requieren reabastecimiento inmediato</p>
                </div>
              </div>
            </CardContent>
          </Card>
        )}

        <div className="mb-6">
          <div className="relative max-w-md">
            <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-muted-foreground" />
            <Input placeholder="Buscar insumos..." className="pl-10" />
          </div>
        </div>

        <Card>
          <CardHeader>
            <CardTitle>Estado del Inventario</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="overflow-x-auto">
              <table className="w-full">
                <thead>
                  <tr className="border-b border-border">
                    <th className="text-left py-3 px-4 font-semibold text-sm">Insumo</th>
                    <th className="text-left py-3 px-4 font-semibold text-sm">Proveedor</th>
                    <th className="text-left py-3 px-4 font-semibold text-sm">Stock Actual</th>
                    <th className="text-left py-3 px-4 font-semibold text-sm">Stock Mínimo</th>
                    <th className="text-left py-3 px-4 font-semibold text-sm">Estado</th>
                    <th className="text-left py-3 px-4 font-semibold text-sm">Tendencia</th>
                    <th className="text-left py-3 px-4 font-semibold text-sm">Acciones</th>
                  </tr>
                </thead>
                <tbody>
                  {insumos.map((item) => {
                    const porcentaje = (item.stock / item.minimo) * 100
                    const estado = porcentaje < 50 ? "Crítico" : porcentaje < 100 ? "Bajo" : "Normal"

                    return (
                      <tr
                        key={item.id}
                        className="border-b border-border last:border-0 hover:bg-muted/50 transition-colors"
                      >
                        <td className="py-3 px-4">
                          <div className="flex items-center gap-2">
                            <Package className="h-4 w-4 text-muted-foreground" />
                            <span className="font-medium">{item.nombre}</span>
                          </div>
                        </td>
                        <td className="py-3 px-4 text-sm text-muted-foreground">{item.proveedor}</td>
                        <td className="py-3 px-4">
                          <span
                            className={`font-semibold ${
                              estado === "Crítico"
                                ? "text-destructive"
                                : estado === "Bajo"
                                  ? "text-yellow-600 dark:text-yellow-400"
                                  : "text-primary"
                            }`}
                          >
                            {item.stock} {item.unidad}
                          </span>
                        </td>
                        <td className="py-3 px-4 text-sm text-muted-foreground">
                          {item.minimo} {item.unidad}
                        </td>
                        <td className="py-3 px-4">
                          <span
                            className={`text-xs px-2.5 py-1 rounded-full font-medium ${
                              estado === "Normal"
                                ? "bg-primary/10 text-primary"
                                : estado === "Bajo"
                                  ? "bg-yellow-100 text-yellow-700 dark:bg-yellow-900/30 dark:text-yellow-400"
                                  : "bg-red-100 text-red-700 dark:bg-red-900/30 dark:text-red-400"
                            }`}
                          >
                            {estado}
                          </span>
                        </td>
                        <td className="py-3 px-4">
                          {item.tendencia === "up" ? (
                            <div className="flex items-center gap-1 text-primary">
                              <TrendingUp className="h-4 w-4" />
                              <span className="text-xs font-medium">Subiendo</span>
                            </div>
                          ) : (
                            <div className="flex items-center gap-1 text-destructive">
                              <TrendingDown className="h-4 w-4" />
                              <span className="text-xs font-medium">Bajando</span>
                            </div>
                          )}
                        </td>
                        <td className="py-3 px-4">
                          <Button size="sm" variant="outline">
                            Reabastecer
                          </Button>
                        </td>
                      </tr>
                    )
                  })}
                </tbody>
              </table>
            </div>
          </CardContent>
        </Card>
      </main>
    </div>
  )
}
